.container {
  padding: 0px 20px;
  margin: 0 auto;
  width: 100%;
  
  @media screen and (min-width: 320px) {
    width: 320px;
     padding: 0px 20px;
  }
  @media screen and (min-width: 768px) {
    width: 768px;
     padding: 0px 32px;
  }
  @media screen and (min-width: 1024px) {
    width: 1024px;
     padding: 0px 32px;
  }
}
